@import 'base.scss';

body{
    overflow: hidden;
}
#header{
    background: $color-4;
    background-position: bottom left;
    background-size: cover;
    position: relative;
    overflow: hidden;
}


#nav-bar{
    height: $navbar-height;
    background: rgba(0, 0, 0, 0.2);
    padding-left: 5%;
    padding-right: 5%;
    
    display: flex;
    align-items: center;
    justify-content: space-between;
}
#logo{
    height: 70%; 

    img{
        height: 100%;
    }
}
.hamburger-container{
    height: 40px;
    width: 40px;
    position: relative;

    span{
        display: inline-block;
        width: 15px;
        height: 2px;
        background: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        transition: all ease-in-out .1s;
    }
    .bar-2{
        left: 15px;
        width: 10px;
    }
    .bar-1::before, .bar-1::after{
        content: '';
        display: inline-block;
        background: #fff;
        width: 30px;
        height: 2px;
        position: absolute;
        top: -10px;
        transform-origin: left;
        transition: all ease-in-out .2s;
    }
    .bar-1::after{
        top: 10px;
    }
    
}
.hamburger-container:hover{
    .bar-2{
        left: 20px;
    }
    .bar-1{
        width: 10px;

        &::before{
            top: -12px;
        }
        &::after{
            top: 12px;
        }
    }
}
.hamburger-click {
    span{
        transition: all ease-in-out .2s;
    }
    .bar-1{
        width: 0!important;
        &::before{ 
            transform: rotateZ(55deg);
            top: -12px;
        }
        &::after{
            transform: rotateZ(-55deg);
            top: 12px;
        }
    }
    .bar-2{
        
        width: 0;
    }
}
