/////*nav-logo*/////
////////////////////
.nav-links-container{
    font-family: $font_size_1;
    z-index: 300;

    position: absolute;
    top: $navbar-height;
    right: 0;
    bottom: 0;
    

    width: 200px;
    display: flex;
    align-items: center;
    flex-direction: column;
    background: $color-3;
    transform: translateX(200px);
    transition: transform ease-in-out .2s;

    .nav-logo{
        width: 150px;
        margin-top: 20px;
        padding-right: 20px;
        padding-left: 20px;
        background: #fff;
        box-shadow: $shadow;

        img{
            width: 100%;
        }
    }
    .nav-links{
        margin-top: 20px;
        a{
            display: inline-block;
            width: 100%;
            padding: 10px;
            background: #fff;
            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
            position: relative;
            transition: color ease-in-out .1s;
            transform: translateX(200px);
            color: $color-2;
            
            &:first-child{
                border-top: 1px solid rgba(0, 0, 0, 0.2);
            }
            &::before{
                content: '';
                display: inline-block;
                background: transparent;
                width: 5px;
                height: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                transition: all ease-in-out .1s;
            }
            &:hover{
                color: $color-1;
                letter-spacing: 1px;
                &::before{
                    background: $color-1;
                }
            }
        }
    }
    p{
        color: $color-2;
        margin-top: 50px;
    }
}
.has-navbar::before{
    content: '';
    width: 100%;
    height: 100%;
    display: inline-block;
    position: absolute;
    top: $navbar-height;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(39, 35, 58, 0.7);
    z-index: 290;
}
.has-navbar .nav-links-container{
    transform: translateX(0);
}
.has-navbar .nav-links a{
    transform: translateX(0);
}
