@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');
////////////
/*variable*/
////////////
$default_font_size :16;
$font_size_1: 'Inter', sans-serif;
$font_size_2: 'Inter', sans-serif;
$container-width: 800;
$color-1: #fd00c5;
$color-2: #27233a;
$color-3: #e7eefb;
$color-4: #14b8fc;
$navbar-height: 60px;
$shadow: 0px 0px 30px 5px rgba(112, 112, 112, 0.4);
$shadow2: 0px 0px 10px 3px rgba(112, 112, 112, 0.4);
$shadow3: 0px 0px 5px 1px rgba(20, 184, 252, 0.5);
////////////
/*variable*/
////////////

@function size($size){
    @return ($size/$default_font_size) * 1rem;
}

%width-content{
    width: 100%;
    padding-left: calc(( 100% - #{$container-width} * 1px) / 2);
    padding-right: calc(( 100% - #{$container-width} * 1px) / 2);

    @media all and (max-width: $container-width * 1px) {
        padding-left: 20px;
        padding-right: 20px;
    }   
}

%width-content-marg{
    max-width: $container-width * 1px;
    margin-left: calc(( 100% - #{$container-width} * 1px) / 2);
    margin-right: calc(( 100% - #{$container-width} * 1px) / 2);
    padding: 20px;

    @media all and (max-width: ($container-width + 20) * 1px) {
        margin-left: 20px;
        margin-right: 20px;
    }
    
}

*, *::before, *::after{
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    box-sizing: border-box;
}
body{
    font-size: $default_font_size * 1px;
    background: $color-3;
    position: relative;
    
}
html{
    overflow-x: hidden!important;
}
a{
    text-decoration: none;
    color: #fff;
}
button, input{
    outline: 0;
}
input:focus{
    box-shadow: $shadow3;
}

/*///////////////////////////*/
/**********Footer*************/
/*///////////////////////////*/

.footer{
    @extend %width-content;
    background: $color-2;
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    padding-bottom: 40px;
    color: #fff;
    font-family: $font_size_1;
    font-size: size(12);
    overflow: hidden;
}
.contact-container{
    flex: none;
    display: flex;
    flex-direction: column;
    width: 30%;

    &>a img{
        width: 40px;
        margin-bottom: 10px;
        transition: transform ease-in-out .1s;

        &:hover{
            transform: scale(1.08);
        }
    }
}
.contact-link{
    position: relative;

    &::after{
        content: '697166769';
        color: $color-4;
        display: inline-block;
        position: absolute;
        font-size: size(16);
        left: 50px;
        top: 10px;
    }
}
.contact-link.phone{
    &::after{
        content: '677610614';
    }
}

.author{
    width: 30%;
    flex: none;
    position: relative;
    padding-left: 10px;

    &>img{
        width: 90px;
        border-radius: 50%;
        border:4px solid $color-1 ;
        transition: border ease-in-out .1s;
    }
    &>img:hover{
        border: 2px solid $color-1;
    }
    a{
        transition: all ease-in-out .1s;
    }
    a:hover{
        color: $color-1;
    }
}
.aside-author{
    position: absolute;
    left: 110px;
    background: $color-1;
    color: #fff;
    opacity: 0;
    border-radius: 5px;
    padding: 10px;
    width: 180px;
    margin-left: 5px;
    transition: opacity ease-in-out .3s;

    &::before{
        content: '';
        display: block;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-right: 10px solid $color-1;
        position: absolute;
        right: 100%;
        top: 50%;
        transform: translateY(-50%);
    }

}
.footer-title{
    margin-bottom: 10px;
    color: $color-4;
}
.footer-title.aside{
    color: #fff;
}
#img-author{
    margin-bottom: 10px;
}
.footer-title{
    font-family: $font_size_2;
    font-size: size(20);
    margin-bottom: 20px;
}

@media all and (max-width: 700px){
    .footer{
        flex-direction: column;
    }
    .author{
        margin-top: 20px;
        width: 100%;
    }
    .contact-container{
        width: 100%;
    }
}
/*///////////////////////////*/
/**********Footer*************/
/*///////////////////////////*/
