@import 'base.scss';

.search-bar-container{
    margin: 0 auto;
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    input{
        width: calc(80% - 20px);
        margin-right: 20px;
        height: 40px;
        border: 1px solid $color-4;
        padding-left: 20px;
        color: lighten($color: $color-2, $amount: 30);
        font-family: $font_size_1;
    }

    button{
        height: 40px;
        width: 20%;
        background: #fff;
        border: 1px solid $color-4;
        border-radius: 0;
        font-family: $font_size_1;
        color: lighten($color: $color-2, $amount: 30);
        overflow-x: hidden;
    }
    button{
        transition: all ease-in-out .1s;
        font-size: size(15);
    }
    button:hover{
        background: $color-4;
        color: #fff;
    }
    @media all and (max-width: 300px){
        button, input{
            font-size: size(12);
        }
    }
}

.link-home{
    height: 40px;
    width: 100px;
    display: inline-block;
    margin-top: 20px;

    div{
        display: flex;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        transition: all ease-in-out .1s;
        padding: 5px;
        padding-right: 15px;
        background: $color-1;

        &:hover{
            background: $color-4;
        }
    }

    svg{
        height: 100%;
        height: 80%;
        transform: rotateZ(-90deg);

        path{
            stroke: #fff;
            stroke-width: 5px;
        }
    }

    p{
        color: #fff;
        font-family: $font_size_1;
    }

}