@import 'base.scss';

.Page-link{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    align-items: center;
    grid-row-gap: 20px;
    padding-bottom: 40px;

    &>a{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-family: $font_size_1;
        box-shadow: $shadow2;
        background: $color-4;
        width: 160px;
        height: 60px;
        transition: all ease-in-out .07s;
        font-size: size(15);
        padding: 4px;

        &:hover{
            background: #fff;
            border: 1px solid $color-4;
            color: $color-2;
        }
    }
}

@media all and (max-width: 550px){
    .Page-link{
        grid-template-columns: 1fr 1fr;
    }
}
@media all and (max-width: 380px){
    .Page-link{
        grid-template-columns: 1fr;

        &>a{
            width: 100%;
        }
    }
}