@import './base';

.download-container{
    @extend %width-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .download-chap-name{
        color: $color-1;
        font-size: size(25);
        font-family: $font_size_2;
        margin-bottom: 40px;

        &>span>span{
            color: $color-4;
            font-size: size(22);
        }
    }

    .link-download{
        display: inline-block;
        background: $color-4;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-family: $font_size_1;
        font-size: size(16);
        padding-left: 10px;
        padding-right: 10px;
        margin-bottom: 40px;
        transition: all ease-in-out .1s;

        &:hover{
            background: $color-1;
        }
    }
}