@import 'base.scss';

.container{
    @extend %width-content;
    font-family: $font_size_2;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 40px;
    
    .title{
        font-size: size(25);
        text-align: center;
        background: #fff;
        color: $color-2;
        border: 1px solid $color-4;
        width: 100%;
        padding: 10px;
        margin-bottom: 40px;
    }
    .section-content{
        font-family: $font_size_1;
        font-size: size(18);
        background: #fff;
        border: 1px solid $color-4;
        width: 100%;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;

        a{
            display: inline-block;
            height: 60px;
            width: 300px;
            background: $color-4;
            margin: 10px auto;
            display: flex;
            align-items: center;
            padding: 0 5px;
            transition: all ease-in-out .2s;

            img{
                width: 50px;
                margin-right: 10px;
            }
            &:hover{
                animation: hover-contact ease-in-out .4s;
                background: $color-1;
            }
            @media all and (max-width: 450px){
                width: 100%;
            }
        }
    }
}

.container .section-content a{
    &>div:nth-child(2){
        display: none;
    }
    &>div:nth-child(3){
        color: #fff;
    }
}

