@import 'base.scss';

.search-result-container{
    margin-top: 40px;
    display: grid;
    grid-row-gap: 20px;
    align-items: center;
    font-family: $font-size-1;
    padding-bottom: 40px;
}
.div-search-result{
    width: 100%;
    height: 60px;
    border: 1px solid $color-4;
    background: #fff;
    overflow: hidden;
    padding: 0 10px;
    transition: all ease-in-out .3s;
}
.title-img{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    p{
        font-family: $font-size-2;
        font-size: size(20);
        color: $color-2;
    }
}
.description-container{
    margin-top: 10px;
    opacity: 0;

    .link-down{
        display: inline-block;
        width: 160px;
        height: 40px;
        background: $color-1;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-width: 0;
        font-family: $font-size-1;
        font-size: size(16);
        margin-top: 5px;
        transition: all ease-in-out .1s;

        &:hover{
            background: $color-4;
            color: #fff;
        }
    }
}
.mat-name{
    margin-left: auto;
    color: $color-1!important;
    font-size: size(14)!important;
    font-style: oblique;
}

/////animation des spans//////
.span-anim{
    width: 40px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotateZ(180deg);
    transition: all ease-in-out .3s;
    border-width: 0;
    background: transparent;

    svg{
        width: 20px;
        height: 20px;

        path{
            stroke: $color-1;
            stroke-width: 5px;
        }
    }
}
.div-has-click{
    height: 180px;

    .span-anim{
        transform: rotateZ(0);
    }
    .description-container{
        opacity: 1;
    }

}

/////animation des spans//////
@media all and (max-width: 550px){
    .mat-name{
        font-size: size(11)!important;
    }
    .title-img p{
        font-size: size(16);
    }
}
@media all and (max-width: 350px){
    .mat-name{
        font-size: size(11)!important;
    }
    .title-img p{
        font-size: size(14);
    }
}
.reveal-search{
    opacity: 0;
    transform: translateY(30px);
}

.reveal-search-visible{
    opacity: 1;
    transform: translateY(0);
    transition: 1s;
}